import { useNavigate } from 'react-router-dom';

const LogoutButton = () => {
    const navigate = useNavigate();

    const handleLogout = () => {
        localStorage.removeItem('token'); //   
        navigate('/'); //    
    };

    return (
        <div className='logout_button section_button ' onClick={handleLogout}><div className='logout_button_text'></div></div>
    );
};

export default LogoutButton;