import React, { useState, useEffect } from "react";
import axios from "axios";
import { apiserver } from "../config";
import AdjustmentsList from "../AdjustmentsList/AdjustmentsList"; //    

const ProjectStagesTable = ({ contractNumber }) => {
    const [stages, setStages] = useState([]);
    const [loading, setLoading] = useState(true);
    const [showAdjustments, setShowAdjustments] = useState(false); //     
    const [activeStageId, setActiveStageId] = useState(null); //    

    useEffect(() => {
        const fetchProjectStages = async () => {
            try {
                const token = localStorage.getItem("token");
                if (!token) {
                    console.error("    localStorage");
                    return;
                }

                //      
                const response = await axios.get(`${apiserver}/projects/list/`, {
                    headers: { Authorization: `Bearer ${token}` },
                });

                //    contract_number
                const projectStages = response.data.filter(
                    stage => stage.contract_number === contractNumber
                );
                setStages(projectStages);
            } catch (error) {
                console.error("    :", error);
            } finally {
                setLoading(false);
            }
        };

        if (contractNumber) {
            fetchProjectStages();
        }
    }, [contractNumber]);

    const getStatus = (isCompleted) => {
        if (isCompleted) {
            return {
                text: "",
                color: "#0A9B19",
                paddingTop: "16.5px",
                paddingBottom: "16.5px",
            }; //  padding   
        } else {
            return {
                text: " ",
                color: "#959595",
                paddingTop: "5px",
                paddingBottom: "5px",
            }; // padding   
        }
    };

    const toggleAdjustments = (stageId) => {
        if (activeStageId === stageId) {
            //     ,  
            setShowAdjustments(!showAdjustments);
            setActiveStageId(null);
        } else {
            // ,     
            setActiveStageId(stageId);
            setShowAdjustments(true);
        }
    };

    return (
        <div>
            <div className="project-stages-table_title">  </div>
            {loading ? (
                <p> ...</p>
            ) : (
                
                <table className="table-project">
                    <thead className="table-header_project">
                        <tr>
                            <th className="table-header_item-none-project" style={{ width: "4%" }}></th>
                            <th className="table-header_item-index"></th>
                            <th className="table-header_item-name"></th>
                            <th className="table-header_item-date-start"> </th>
                            <th className="table-header_item-date-end"> </th>
                            <th></th>
                            <th className="table-header_item-date-fact">  </th>
                        </tr>
                    </thead>
                    <tbody>
                        {stages.length > 0 ? (
                            stages.map((stage, index) => {
                                const { text, color } = getStatus(stage.is_completed);
                                const isDesignApproval = stage.name === "   ";
                                const isActive = activeStageId === stage.id && showAdjustments; //   

                                return (
                                    <React.Fragment key={stage.id}>
                                        <tr
                                            className={`table-header_item-tr ${isActive ? 'soglas-active' : ''}`} //   soglas-active    
                                            onClick={isDesignApproval ? () => toggleAdjustments(stage.id) : null}
                                            style={{ cursor: isDesignApproval ? "pointer" : "default" }}
                                        >
                                            <td>
                                                {/* SVG-    "   " */}
                                                {isDesignApproval && (
                                                    <img
                                                        className={`toggle-icon ${showAdjustments && isActive ? "rotate-180" : ""}`} //    
                                                        src="/chevron-right1.svg" //    
                                                        alt="Folder Icon"
                                                        width="30" //  
                                                        height="30" //  
                                                        style={{ cursor: "pointer" }}
                                                        onClick={e => { e.stopPropagation(); toggleAdjustments(stage.id); }} //  
                                                    />
                                                )}
                                            </td>
                                            <td className="table-header_item-index">{index + 1}</td>
                                            <td className="table-header_item-name">
                                                {stage.name}
                                            </td>
                                            <td className="table-header_item-date">
  {stage.start_date ? stage.start_date.split(" ")[0] : ""}
</td>
<td className="table-header_item-date">
  {stage.deadline ? stage.deadline.split(" ")[0] : ""}
</td>
                                            <td
                                                className="table-header_item-status"
                                                style={{
                                                    color,
                                                    paddingTop: getStatus(stage.is_completed).paddingTop,
                                                    paddingBottom: getStatus(stage.is_completed).paddingBottom,
                                                }}
                                            >
                                                {text}
                                            </td>
                                            <td className="table-header_item-date">
  {stage.actual_date ? stage.actual_date.split(" ")[0] : ""}
</td>
                                        </tr>
                                        {/*   - "   ",    */}
                                        {isDesignApproval && isActive && (
                                            <tr>
                                                <td colSpan="7">
                                                    <AdjustmentsList contractNumber={contractNumber} />
                                                </td>
                                            </tr>
                                        )}
                                    </React.Fragment>
                                );
                            })
                        ) : (
                            <tr>
                                <td colSpan="7">   </td>
                            </tr>
                        )}
                    </tbody>
                </table>
            )}
        </div>
    );
};

export default ProjectStagesTable;
