import React, { useState, useEffect, useRef } from "react";
import { useNavigate } from "react-router-dom";
import axios from "axios";
import { apiserver } from "../config";

const ProfileMenu = () => {
    const [menuOpen, setMenuOpen] = useState(false);
    const [username, setUsername] = useState(""); //   
    const navigate = useNavigate();
    const menuRef = useRef(null); //    

    useEffect(() => {
        const fetchUsername = async () => {
            try {
                const token = localStorage.getItem("token");

                if (!token) {
                    console.error(" ");
                    return;
                }

                const response = await axios.get(`${apiserver}/auth/users/`, {
                    headers: { Authorization: `Bearer ${token}` },
                });

                if (response.data.length > 0) {
                    setUsername(response.data[0].username); //     API
                    localStorage.setItem("username", response.data[0].username); //   localStorage
                }
            } catch (error) {
                console.error("     :", error);
                const storedUsername = localStorage.getItem("username");
                if (storedUsername) {
                    setUsername(storedUsername); //   ,  
                }
            }
        };

        fetchUsername();
    }, []);

    const toggleMenu = () => {
        setMenuOpen(!menuOpen);
    };

    const handleLogout = () => {
        localStorage.removeItem("token"); //  
        localStorage.removeItem("username"); //   
        navigate("/"); //    
    };

    //       
    useEffect(() => {
        const handleClickOutside = (event) => {
            if (menuRef.current && !menuRef.current.contains(event.target)) {
                setMenuOpen(false);
            }
        };

        if (menuOpen) {
            document.addEventListener("mousedown", handleClickOutside);
        } else {
            document.removeEventListener("mousedown", handleClickOutside);
        }

        return () => {
            document.removeEventListener("mousedown", handleClickOutside);
        };
    }, [menuOpen]);

    return (
        <div className="profile-menu-container" ref={menuRef}>
            <button className="profile-button" onClick={toggleMenu}>
                <div className="heder_menu-object_form-feedback profile-icon"></div>
            </button>

            {menuOpen && (
                <div className="profile-dropdown">
                    <img className="dropdown-toggle" src="/dropdown_toggle.svg" alt="" />
                    <div className="profile-info">
                        <div>
                            <img src="/user.svg" alt="" className="user-icon" />
                        </div>
                        <div className="profile-username">{username}</div>
                    </div>
                    <button className="logout-button" onClick={handleLogout}>  </button>
                </div>
            )}
        </div>
    );
};

export default ProfileMenu;
